<template>
  <div>
    <div class="utils__title mb-3">
      <strong class="text-uppercase font-size-16">Total Statistics</strong>
    </div>
    <div class="row">
      <div class="col-xl-3" v-for="(chartCard, index) in chartCardData" :key="index">
        <cui-chart-card
          :title="chartCard.title"
          :amount="chartCard.amount"
          :chartData="chartCard.chartData"
          :url="chartCard.url"
        />
      </div>
    </div>
    <div class="row">
      <div class="col-lg-12">
        <div class="card">
          <div class="card-header">
            <div class="utils__title">
              <strong>Total Customers</strong>
            </div>
            <div class="utils__titleDescription">List of all customers information</div>
          </div>
          <div class="card-body">
            <a-table
              :columns="referalsColumns"
              :dataSource="referalsData"
              :pagination="false"
              class="utils__scrollTable"
              :scroll="{ x: '100%' }"
            ></a-table>
          </div>
        </div>
      </div>
    </div>
    <div class="utils__title utils__title--flat mb-3">
      <strong class="text-uppercase font-size-16">Recent Transactions (167)</strong>
      <router-link to="/dashboard/history/transactions">
        <a-button class="ml-3">View All</a-button>
      </router-link>
    </div>
    <div class="row">
      <div class="col-lg-12">
        <cui-payment-transaction
          v-for="(paymentTransaction, index) in transactions"
          :key="index"
          :income_usd="paymentTransaction.total_usd"
          :income_ngn="paymentTransaction.total_ngn"
          :amount="paymentTransaction.report_paid_amount"
          :status="paymentTransaction.report_status"
          :info="paymentTransaction.client_full_name"
          :transactCode="paymentTransaction.transaction_trans_code"
        />
        <!--        <div class="text-center pb-5">-->
        <!--          <a-button type="primary" class="width-200" :loading="true">Load More...</a-button>-->
        <!--        </div>-->

      </div>
    </div>
  </div>
</template>

<script>
import data from './data.json'
import CuiChartCard from '@/components/CleanUIComponents/ChartCard'
import CuiPaymentTransaction from '@/components/CleanUIComponents/PymentTransaction'

const referalsColumns = [
  {
    title: 'Customer Name',
    dataIndex: 'customer_name',
    key: 'customer_name',
  },
  {
    title: 'Business Name',
    dataIndex: 'business_name',
    key: 'business_name',
  },
  {
    title: 'Email Address',
    dataIndex: 'email',
    key: 'email',
    sorter: (a, b) => a.email - b.email,
  },
  {
    title: 'Phone Number',
    dataIndex: 'phone',
    key: 'phone',
  },
  {
    title: 'Address',
    dataIndex: 'location',
    key: 'location',
  },
  {
    title: 'Date',
    dataIndex: 'date',
    key: 'date',
    sorter: (a, b) => a.date - b.date,
  },
]

export default {
  components: {
    CuiPaymentTransaction,
    CuiChartCard,
  },
  data() {
    return {
      chartCardData: {},
      paymentTransactionsData: data.paymentTransactionsData,
      progressCardsData: data.progressCardsData,
      referalsData: [],
      referalsColumns,
      transactions: {},
    }
  },
  mounted() {
    this.getAnalyticsBasic()
    this.getAllCustomers()
    this.getAllTransactions()
  },
  methods: {
    getAnalyticsBasic() {
      this.axios.get('analytics/basic')
        .then(response => {
          console.log(response.data.data)
          const basicData = response.data.data
          const chartCardData = [
            {
              title: 'Shipment Orders',
              amount: basicData.total_shipments.toString(),
              url: '/dashboard/shipment',
              chartData: {
                series: [
                  [
                    2,
                    11,
                    8,
                    14,
                    18,
                    20,
                    26,
                  ],
                ],
              },
            },

            {
              title: 'Issues',
              amount: basicData.total_issues.toString(),
              url: '/dashboard/issues/',
              chartData: {
                series: [
                  [
                    2,
                    11,
                    8,
                    14,
                    18,
                    20,
                    26,
                  ],
                ],
              },
            },

            {
              title: 'Customers',
              amount: basicData.total_clients.toString(),
              url: '/dashboard/customer_data',
              chartData: {
                series: [
                  [
                    42,
                    40,
                    80,
                    67,
                    84,
                    74,
                    97,
                  ],
                ],
              },
            },

            {
              title: 'Expenditure',
              amount: '₦' + basicData.total_expenditure.toString(),
              url: '/dashboard/reports',
              chartData: {
                series: [
                  [
                    42,
                    40,
                    80,
                    67,
                    84,
                    74,
                    97,
                  ],
                ],
              },
            },

          ]
          this.chartCardData = chartCardData
        })
        .catch(error => {
          console.log(error)
        })
    },
    getAllCustomers() {
      this.axios.get('clients')
        .then(response => {
          // console.log('clients', response.data.data.client)
          const dt = response.data.data.client
          const data = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              customer_name: dt[i].full_name,
              business_name: dt[i].business_name,
              phone: dt[i].phone,
              email: dt[i].email,
              location: dt[i].address,
              date: dt[i].created_at,
              id: dt[i].id,
              key: dt[i].id,
              action: {
                name: 'Delete',
                url: dt[i].id,
              },
            }
            data.push(userData)
          }
          this.referalsData = data
        })
        .catch(error => {
          console.log(error)
        })
    },
    getAllTransactions() {
      this.axios.get('transaction/all')
        .then(response => {
          console.log('transactions ', response.data.data.transactions)
          const dt = response.data.data.transactions
          this.transactions = dt.slice(0, 3)
        })
        .catch(error => {
          console.log(error)
        })
    },
  },

}
</script>
